import { useEffect, useState } from "react";
import { Card, Col, Button } from "react-bootstrap";
import Web3 from "web3";
import { marketplace } from "../../abis/marketplace";
import { nftcontract } from "../../abis/nftcontract";
import { erc20token } from "../../abis/erc20token";

const web3 = new Web3(Web3.givenProvider);
const marketplaceContracrAddress = "0xB63Ba01F9aEbf5B297777106201ff9a1d7B3ABCa";
const nftContractAddress = "0x2723C56341C4Da914453Ea1dF140cf5959875C01";
const erc20Address = "0xB4B982A691C7d091775CeBEC869AB0d780fe1E65";

const marketPlaceContract = new web3.eth.Contract(
  marketplace,
  marketplaceContracrAddress
);

const nftContractDetail = new web3.eth.Contract(
  nftcontract,
  nftContractAddress
);

const erc20contract = new web3.eth.Contract(erc20token, erc20Address);

const DisAllowNftList = (props) => {
  console.log("disallow",props)
  const [metaData, setMataData] = useState([]);
  const [price, setPriceData] = useState("");
  const [isValid, setIsValid] = useState(false);
  const [nftID, setNftID] = useState("");

  useEffect(() => {
    getNftData();
  }, []);

  const getNftData = async () => {
    const getresultData = await getResult();

    if (getresultData > 0) {
      setIsValid(true);
      const response = await fetch(props.data.token_uri);
      setPriceData(props.data.amount);
      setNftID(props.data.token_id);
      setMataData(await response.json());
    }
  };

  const getResult = async () => {
    return await marketPlaceContract.methods
      .tokenIdToPrice(props.data.token_id)
      .call();
  };

  const allowTokenHandler = async () => {
    const accountBalance = await erc20contract.methods
      .balanceOf(localStorage.getItem("walletAddress"))
      .call();

    erc20contract.methods
      .approve(marketplaceContracrAddress, web3.utils.fromWei(accountBalance))
      .send({ from: localStorage.getItem("walletAddress") })
      .then(function (data) {
        console.log(data);
      });
  };

  const buynftHandler = async (id, price) => {
    marketPlaceContract.methods
      .buy(id, price)
      .send({ from: localStorage.getItem("walletAddress") })
      .then(function (data) {
        console.log("Data ", data);
      });
  };

  return (
    <>
      {isValid && (
        <Col lg={4} className="py-lg-5">
          <Card className="shadow">
            <Card.Img variant="top" height={"150px"} src={metaData.image} />
            <Card.Body>
              <Card.Title>{metaData.name}</Card.Title>
              <Card.Text>
                {metaData.Description
                  ? metaData.Description.trim(0, 10)
                  : "Not Found"}
              </Card.Text>
              <Card.Title>{price}</Card.Title>
            </Card.Body>

            {/* <Card.Body>
              <Button className="w-100" onClick={() => allowTokenHandler()}>
                Allow to spend token
              </Button>
            </Card.Body>
            <Card.Body>
              <Button
                className="w-100"
                onClick={() => buynftHandler(nftID, price)}
              >
                Buy NFT
              </Button>
            </Card.Body> */}
            <Card.Body>
              <Button
                className="w-100"
                onClick={() => buynftHandler(nftID, price)}
              >
                DisAllow NFT
              </Button>
            </Card.Body>
          </Card>
        </Col>
      )}
    </>
  );
};
export default DisAllowNftList;
