import { Row,Col,Button } from "react-bootstrap";
import ButtonNft from "./ButtonNft";
import {Routes, Route, useNavigate} from 'react-router-dom';

const CardButton = (props) => {
  const navigate = useNavigate();
  const routeChangeSell = () =>{ 
    navigate('/sell');
  }
  const routeChangeBuy = () =>{ 
    navigate('/buy');
  }
  
  return (
    <Row className={props.rowClass}>
      <Col className={props.colClass}>
        <Button className={props.buttonBg1}  onClick={routeChangeBuy}>Buy NFT</Button>
      </Col>
      <Col className={props.colClass}>
        <Button className={props.buttonBg2} onClick={routeChangeSell}>Sell NFT</Button>
      </Col>
    </Row>
  );
};
export default CardButton;
