import { useEffect, useState } from "react";
import { Form, Row, Col, Container, Button, Card } from "react-bootstrap";
import Web3 from "web3";
import { marketplace } from "../../abis/marketplace";
import { nftcontract } from "../../abis/nftcontract";

const NftCardListSell = (props) => {
  console.log("line 5 ", props.data);

  const web3 = new Web3(Web3.givenProvider);

  const [metaData, setMataData] = useState([]);
  const [price, setPriceData] = useState("");
  const [nftID, setNftID] = useState("");
  const [allowNft, setAllowNft] = useState(false);

  useEffect(() => {
    getNftData();
  }, []);

  const marketplaceContracrAddress =
    "0xB63Ba01F9aEbf5B297777106201ff9a1d7B3ABCa";
  const nftContractAddress = "0x2723C56341C4Da914453Ea1dF140cf5959875C01";
  const marketPlaceContract = new web3.eth.Contract(
    marketplace,
    marketplaceContracrAddress
  );

  const nftContractDetail = new web3.eth.Contract(
    nftcontract,
    nftContractAddress
  );

  const allowNFTHandler = async (id) => {
    console.log(id);
    const result = await nftContractDetail.methods
      .approve(marketplaceContracrAddress, id)
      .send({ from: localStorage.getItem("walletAddress") })
      .then(function (data) {
        setAllowNft(true);
        console.log("Data ", data);
      });
    console.log(result);
  };

  const setprice = async (id, price) => {
    marketPlaceContract.methods
      .allowBuy(id, price)
      .send({ from: localStorage.getItem("walletAddress") })
      .then(function (data) {
        console.log("Data ", data);
      });
  };

  const getNftData = async () => {
    const response = await fetch(props.data.token_uri);
    setMataData(await response.json());
    setNftID(props.data.token_id);
  };

  const onPriceHandler=(event)=>{
    setPriceData(event.target.value);
  }

  console.log(metaData);

  return (
    <Col sm={4}>
      <Card style={{ width: "18rem" }}>
        <Card.Img
          variant="top"
          src={metaData.image ? metaData.image : "NULL"}
        />
        <Card.Body>
          <Card.Text>{metaData.name ? metaData.name : "NULL"}</Card.Text>
          <Card.Title>
            {metaData.Description ? metaData.Description : "NULL"}
          </Card.Title>
        </Card.Body>
        <Form>
          <Form.Group>
            <Form.Label htmlFor="inlineFormInput" visuallyHidden>
              Emnter An Token Id
            </Form.Label>
          </Form.Group>
          <Form.Group>
            <Form.Label htmlFor="inlineFormInput" visuallyHidden>
              Enter An Price
            </Form.Label>
            <Form.Control
              className="mb-2"
              id="inlineFormInput"
              placeholder="Enter Price"
              onChange={onPriceHandler}
              disabled={!allowNft}
            />
          </Form.Group>

          {!allowNft && (
            <Button className="w-100" onClick={() => allowNFTHandler(nftID)}>
              Allow to sell
            </Button>
          )}

          {allowNft && (
            <Button className="w-100" onClick={() => setprice(nftID,price)}>
              Sell NFT
            </Button>
          )}
        </Form>
      </Card>
    </Col>
  );
};
export default NftCardListSell;
